.contact {
    &-shake{
        animation: 3s shake infinite alternate;
    }
    &-icon {
        width: 75px;
        height: 75px;
        background-image: url("../images/info.png");
        background-size: cover;
    }
    &__headline {
        font-size: 24px;
        font-weight: 600;
        padding-bottom: 30px;
    }
    &__sub-headline {
        font-size: 18px;
        padding-bottom: 30px;
    }
}

@media (min-width: 1024px) {
    .contact {
        &-icon {
            width: 250px;
            height: 250px;
        }
        &__headline {
            font-size: 40px;
        }
        &__sub-headline {
            font-size: 32px;
        }
    }
}

/*Animation Shake*/
@keyframes shake {
    0% {
        transform: skewX(-3deg);
    }
    1% {
        transform: skewX(3deg);
    }
    2% {
        transform: skewX(-3deg);
    }
    3% {
        transform: skewX(3deg);
    }
    4% {
        transform: skewX(-3deg);
    }
    5% {
        transform: skewX(0);
    }
    100% {
        transform: skewX(0);
    }
    
}
